import { AdminService } from './../../../services/backend/admin.service';
import { Component, OnInit,OnDestroy } from '@angular/core';
import { HttpClient,HttpErrorResponse } from '@angular/common/http';
import {Router} from '@angular/router';
import { Subject } from 'rxjs';

@Component({
  templateUrl: 'list.component.html'
})
export class ListComponent implements OnDestroy, OnInit {

 dtOptions: DataTables.Settings = {};
  plans: any;
  dtTrigger: Subject<any> = new Subject();

  constructor(private http: HttpClient, private service: AdminService,private router: Router) {}

  ngOnInit(): void {
	  
	this.dtOptions = {
        pagingType: 'full_numbers',
		pageLength: 3,
        columnDefs: [ { orderable: false, targets: [3] } ]
    };  
	// Call gertuser service function 
	 
	this.service.getPlans().subscribe(res => {
		console.log(res);
	   this.plans = res.data;
	   this.dtTrigger.next();
    },err=>{		
		console.log(err); //   this.router.navigate(['login']);
	});
	  
  }
  ngOnDestroy(): void {
    // Do not forget to unsubscribe the event
    this.dtTrigger.unsubscribe();
  } 
}